/* --------- BootStrap Style Overrides ----------- */
.accordion-body {
    padding: inherit !important;
}

.accordion-header {
    position: relative;
}

.accordion-button::after {
    position: absolute;
    right: 10px;
    content: '\f078';
    font-family: 'Font Awesome 5 Pro';
    font-size: 1rem;
    font-weight: 300;
    color: var(--immortalBlack);
    background: none;
    top: 13px;
}
.activeCallstag .accordion-button::after {
    right: 0;
    top: 0;
}

.accordion-button:focus {
    box-shadow: none;
}

.accordion .accordion-button:not(.collapsed) {
    /* color: var(--color1); */
    background: var(--accordion-active);
    border-bottom: 1px solid var(--border-color);
    box-shadow: none;
}

.accordion .accordion-button:not(.collapsed)::after {
    content: '\f078';
    font-family: 'Font Awesome 5 Pro';
    font-size: 1rem;
    font-weight: 300;
    color: var(--ui-accent);
    background: none;
}

/* --------- BootStrap Style Overrides ----------- */

