.menuWrapper ul .tabItem {
    margin: 0 !important;
    padding: 0px 0px !important;
    height: 31px;
}

.menuWrapper ul .tabItem a {
    color: #fff;
    text-decoration: none;
    font-size: 0.8125rem;
    font-weight: 300;
}

