/* --------- Tab Content Common Styles ---------- */
.tab-content .title {
    font-size: 1.25rem;
    font-weight: 500;
}

/* --------- Tab Content Common Styles ---------- */

/* SearchBox Text */
#phonePage .searchBox span {
    position: absolute;
    top: 5px;
    left: 25px;
    font-weight: 300;
    /* color: var(--ui-accent); */
    color: #fff;
}

/* SearchBox Icon */
#phonePage .searchBox i {
    font-size: 0.875rem;
    color: var(--ui-accent);
}

/* Input Search */
#phonePage #headerSearch {
    border: 1px solid var(--bg-color);
    border-radius: 50px;
    padding: 5px 30px;
    background: var(--bg-color) url("../../images/search_b.png") no-repeat 7px center / 17px 17px;
    width: 100%;
    outline: none;
    /* transition: 0.3s ease-out; */
    margin: 0px 10px 0px 0px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 5px;
}

.masterList #headerSearch {
    padding: 2px 5px 2px 30px !important;
}

#phonePage #headerSearch::placeholder {
    color: #868686;
    font-weight: 300;
    position: relative;
    top: 1px;
}

#phonePage #headerSearch:focus,
#phonePage #headerSearch:hover {
    border: 1px solid var(--ui-accent);
}

[type=search] {
    outline-offset: 0px;
}

/* -------- Call List Item Section ---------- */
#phonePage .tab-content .callListItem {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 10px;
}

/* Call List Item Image */
#phonePage .callListItem .profileHolder {
    overflow: hidden;
    width: 45px;
    height: 45px;
    margin-left: 15px;
    padding: 0;
}

/* -------- Call List Item Section ---------- */