.panelButton {
  width: 100px;
  height: 35px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 5px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
  /* background: var(--nav-color); */
  background: var(--ui-accent);
  padding: 0;
  text-decoration: none;
  margin-left: 10px;
  position: relative;
}

.panelButton.addTag {
  height: 30px;
}

