  background-color: #3caa45;
}

.panelButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.panelButton:disabled .icon i {
  transform: none !important;
}

.panelButton,
.panelButton span {
  transition: 200ms;
}

.panelButton .text {
  transform: translateX(10px);
  color: white;
  font-weight: 500;
  font-size: 0.875rem;
}

.panelButton .icon {
  position: absolute;
  border-left: 1px solid #ffffff75;
  transform: translateX(65px);
  height: 35px;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff !important;
  font-size: 1rem;
}

.panelButton:hover {
  /* background: linear-gradient(195deg, #ff3636, #c90000); */
  background: #474747;
}

.panelButton:hover .text {
  color: transparent;
}

.panelButton:hover .icon {
  width: 100%;
  border-left: none;
  transform: translateX(0);
}

.panelButton:focus {
  outline: none;
}

.panelButton:active .icon i {
  transform: scale(0.8);
}

.panelButton.static {
  display: block;
  width: 100%;
  margin: 0;
  width: fit-content;
  padding-inline: 15px;
}

.panelButton.static:hover .text {
  color: #fff
}

