.tableButton.nottif::after {
  content: '\f071';
  font-family: 'FontAwesome';
  font-size: 0.625rem;
  color: #ffffff;
  position: absolute;
  top: -7px;
  background: red;
  border-radius: 50%;
  right: -8px;
