}

.skeleton-button {
  width: 100px;
  height: 35px;
  border-radius: 5px;
}

.skeleton-heading {
  width: 200px;
  height: 24px;
}

.skeleton-heading-small {
  height: 16px;
  width: 290px;
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 70%);
  }

  100% {
    background-color: hsl(200, 20%, 95%);
  }
}

